<template>
  <div id="main_photo">
    <div :style="bg">
    </div>
    <Search style="z-index: 5;position: absolute;top:255px"></Search>
  </div>
</template>

<script>
import Search from './Search'
export default {
  name: 'backPhoto',
  components: {Search},
  data () {
    return {
      bg: {
        backgroundImage: 'url(' + require('../../../static/image/bg.jpg') + ')'
      }
    }
  }
}
</script>

<style scoped>
  #main_photo{
     width: 100%;
     height: 500px;
    position: relative;
   }
  #main_photo>div{
    width: 100%;
    height: 100%;
  }
  #main_photo img{
    width: 100%;
    height: 100%;
  }
</style>



