<template>
    <div>
      <div class="reccomends">
         <reccomend class="singlereccomend" v-for="(item,index) of reccomends" :key="index" :item="item"></reccomend>
      </div>
    </div>
</template>

<script>
  import Reccomend from '../User/Reccomend'
  export default {
    name: 'Reccomends',
    components: {Reccomend},
    data(){
      return{
        reccomends:[]
      }
    },
    created () {
      console.log("正在获取某人所有作品"+this.$store.state.to_id);
        this.$api.article.getArticles(this.$store.state.to_id).then(res=>{
            this.reccomends=res.data;
            console.log("他的作品第一个的title"+this.reccomends[0].title);
        });
    }
  }
</script>

<style scoped>
.reccomends{
  width: 96%;
  padding: 0 2%;
  display: flex;
  justify-content:flex-start;
  flex-wrap: wrap;
  background-color: transparent;
}
  .singlereccomend{
    background-color: white;
    width: 15%;
    margin-bottom: 25px;
  }
</style>



