<template>
  <div id="boxes" class="boxes" :style="{height: h+'px',overflow:showroll?'auto':'hidden'}" @mouseenter="enter" @mouseleave="leave">
      <waterfall-box :pinw="pinw" :showuser="showuser" v-for="(part,index) in parts" :key="part.id" ref="col" :style="{left:part.left + 'px', top: part.top + 'px'}" :box="part" v-on:set="doSort" :index="index"></waterfall-box>
  </div>
</template>

<script>
import waterfallBox from './waterfallBox'
let num=0;
let i=0;
let compareArray = [];
let nodeList;
let  begin=0;
export default {
  name: 'waterfall',
  components: {waterfallBox},
  props:['pinw','gap','showuser','up_gap','parts','roll'],
  data () {
    return {
      h: 300,
      showroll:false
    }
  },
  updated(){
    nodeList = this.$refs.col;
    num=Math.floor((this.$el.offsetWidth+this.gap)/(this.pinw+this.gap));
    if(!this.roll)
      begin=(this.$el.offsetWidth+this.gap-(this.gap+this.pinw)*num)/2;
  },
  methods:{
     enter(){
       if(this.roll)
       this.showroll=true;
     },
    leave(){
      if(this.roll)
        this.showroll=false;
    },
     doSort(offsetHeight,index){
       setTimeout(() => {
         if(i<num){
           //成行元素
           compareArray[i] =nodeList[index].$el.offsetHeight;
           if(i>0){
             this.parts[i].left=this.parts[i-1].left+this.pinw+this.gap;
           }else{
             this.parts[i].left=begin;
           }
         }else{
           //获取成行元素中高度最低的值
           let minHeight = Math.min.apply('',compareArray);
           //alert(compareArray + ",min=" + minHeight);
           //获取成行元素中高度最低元素的索引
           let minkey = this.getMinHeightKey(compareArray,minHeight);
           //为新增的瀑布流块设置定位
           this.parts[i].top=minHeight+this.up_gap;
           //设定新增加的瀑布流块的top和left
           this.parts[i].left =this.parts[minkey].left;
           //将该索引位置的高度改变为新增后的高度[原来瀑布流块的高度+新增的瀑布流块的高度]
           compareArray[minkey] += nodeList[i].$el.offsetHeight+this.up_gap;
         }
         i++;
         if(i===nodeList.length && !this.roll){
           this.h=this.getHeight(compareArray)+20;
         }
       }, 300);


       }
       ,
                        getMinHeightKey(arr,minH){
                         for( let j=0;j<arr.length; j++){
                          if(arr[j] === minH){
                           return j;
                             }
                         }
                       },
        getHeight(arr){
           let max=0;
          for( let j=0;j<arr.length; j++){
            if(arr[j] >max){
              max=arr[j];
            }
          }
          return max;
        }
  }
}

</script>

<style scoped>
.boxes{
  position: relative;
  width: 96%;
  display:inline-block;
}
</style>



