<template>
  <div>
    <div class="reccomend" @mouseenter="isHover=true" @mouseleave="isHover=false" :style="{'filter':isHover?'drop-shadow(4px 2px 4px rgba(166,166,166,0.6))':''}">
      <div class="title">{{item.title}}</div>
        <photo class="threeP" :img="img[0]" :pb="100"></photo>
        <div class="threeP" style="margin-top: 3px">
          <photo class="P" :img="img[1]" :pb="100"></photo>
          <photo class="P" :img="img[2]" :pb="100"></photo>
          <photo class="P" :img="img[3]" :pb="100"></photo>
        </div>
        <div class="user">
          <div style="display: flex;">
            <photo :img="item.user_photo" :pb="100" style="width: 25px" ></photo>
            <span style="font-size: 12px;height: 35px;line-height: 25px">{{item.name}}</span>
          </div>
           <div></div>
          <div class="focus">已关注</div>
        </div>
    </div>
  </div>

</template>

<script>
  import Photo from '../public/photo'
  export default {
    name: 'Reccomend',
    components: {Photo},
    props:['item'],
    computed:{
       img(){
         let imgs=JSON.parse(this.item.imgs);
         console.log(imgs[0]);console.log(imgs[1]);console.log(imgs[2]);console.log(imgs[3]);
         console.log(this.item.user_photo);
           return imgs;
       }
    },data(){
      return{
        isHover:false
      }
    }
  }
</script>

<style scoped>
.reccomend{
  width:100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-sizing: border-box;
  padding: 2% 1.5%;
  align-items: center;
  position: relative;
  border: 1px solid rgba(217,217,217,0.4);
  transition: transform 0.2s;
  margin: 10px;
}
.reccomend:hover{
  transform: translate(0,-2px);
}
  .threeP{
    width: 96%;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    }
  .P{
    width: 32.5%;
  }
  .user{
    display: flex;
    width: 96%;
    box-sizing: border-box;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 35px;
    margin-top: 5px;
  }
  .focus{
      height: 35px;
      width: fit-content;
    text-align: center;
    line-height: 25px;
    font-size: 14px;
  }
  .title{
    position: absolute;
    top:1.8%;
    left:3.5%;
    color: white;
    font-size: 14px;
    z-index: 2;
    font-weight: bold;
  }
</style>



