<template>
  <div style="background-color: rgba(9,7,35,1)">
      <Search class="search" @searchResult="getResult"> </Search>
      <card-fall class="waterfall" :cards="cards"></card-fall>
    <div style="display: flex;justify-content: center;width: 400px;height: 300px;align-items: center;position: relative;left: 50%;transform: translate(-50%,0)" >
      <last-page style="margin-right: 40px"></last-page>
      <next-page></next-page>
    </div>
  </div>
</template>
<script>
  import Guide from './guide'
  import BackPhoto from '../HomePage/backPhoto'
  import Waterfall from '../HomePage/waterfall'
  import Search from '../HomePage/Search'
  import CardFall from '../HomePage/CardFall'
  import Collections from '../Content/Collections'
  import LastPage from '../HomePage/LastPage'
  import NextPage from '../HomePage/NextPage'
  export default {
    name: 'HomePage',
    components: {
      NextPage,
      LastPage,
      Collections,  CardFall, Search, Waterfall, BackPhoto, Guide
    },
    data(){
      return{
        cards: [],
        currentView:'home'
      }
    },
    created(){
         this.$store.commit('reLogin');
        this.$api.action.home(1).then(res=>{
              if(res.data.status==='1'){
                this.cards=res.data.data;
              }

        })
    },
    mounted(){

    },
    methods:{
          getResult(data){
            this.cards=data;
          }
    }

  }
</script>

<style scoped>
  .search{
    padding: 150px 0;
  }
  .waterfall{
     padding: 20px;
     width: 100%;
  }
  .next{

  }
</style>



