<template>
    <div class="next">
      <div class="right"></div>
      <div class="right"></div>
    </div>
</template>

<script>
  export default {
    name: 'NextPage'
  }
</script>

<style scoped>
.next{
   width: 180px;
  height: 50px;
  background-color: #999999;
  transition: background-color  1s,padding-left 0.5s;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: 5px;
}
.next:hover{
  background-color: #FFE900;
  padding-left: 80px;
}
.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 10px;
  margin-top: 15px;
}

</style>



