<template>
<div >
      <div class="container">
            <router-link to="/user" v-for="(item,index) of cards" :key="index" class="singlecard" >
               <card-item style="width: 100%" :card="item" @click.native="go(item)"></card-item>
            </router-link>
      </div>
</div>
</template>

<script>
  import CardItem from './CardItem'
  import LastPage from './LastPage'
  import NextPage from './NextPage'
  export default {
    name: 'CardFall',
    props:['cards'],
    components: {NextPage, LastPage, CardItem},
    data(){
      return{
      }
    },methods:{
        go(item){
          this.$store.commit('setView','2');
          this.$store.commit('reSetTo',item);
        }
    }
  }
</script>

<style scoped>
  .container{width: 90%;  background:transparent;position: relative;left: 5%;justify-content: flex-start;display: flex;flex-wrap: wrap;flex-direction: row}
  .singlecard{
    margin: 20px 15px;
    width: 30%;
  }
</style>



