<template>
     <div class="info">
          <avator  style="width: 80px;margin-bottom: 50px;" ref="user_photo" @change="photo_change=true"></avator>
          <div style="color: black">{{this.$store.state.currentUser.ID}}</div>
          <div class="item">
               <div class="label">昵称</div>
               <input type="text" v-model="name">
          </div>

       <div class="item">
         <div class="label">性别</div>
         <input type="text" v-model="sex">
       </div>
       <div class="item">
         <div class="label">密码</div>
         <input type="password" v-model="PSW">
       </div>
       <div class="item">
         <div class="label">新密码</div>
         <input type="password" v-model="NewPSW">
       </div>
       <div class="item">
         <div class="label">个性签名</div>
         <input class="sign"  v-model="sign">
       </div>
       <div class="action">
         <sb  style="width: 60px;border-radius: 20px;overflow: hidden" :bg1="'#DDDDDD'" :bg2="'#FF0000'" :color1="'#000000'" :color2="'#ffffff'" :content="'保存'" @click.native="updateUser"></sb>
         <sb  style="width: 60px;border-radius: 20px;overflow: hidden" :bg1="'#DDDDDD'" :bg2="'#FF0000'" :color1="'#000000'" :color2="'#ffffff'" :content="'取消'" @click.native="cancel"></sb>
       </div>
     </div>
</template>

<script>
  import Avator from '../User/avator'
  import Sb from '../public/sb'
  import V_div from '../public/v_div'
  export default {
    name: 'UserInfo',
    components: {V_div, Sb, Avator},
    data(){
      return{
        isSet:false,
        change:false,
        photo_change:false,
             name:'',
             PSW:'',
             sex:'',
             NewPSW:'',
             sign:'',
             img:''
      }
    },
    mounted () {
       let user=this.$store.state.currentUser;
       if(user.name===undefined)
         this.name='';
       else
       this.name=user.name;
       this.sex=user.sex;
      if(user.sign===undefined)
        this.sign='';
      else
       this.sign=user.sign;
    },
    methods:{
         updateUser(){
           let formData=new FormData();
           if(this.photo_change)
             formData.append('user_photo',this.$refs.user_photo.fil);
           formData.append('name',this.name);
           formData.append('sex',this.sex);
           formData.append('sign',this.sign);
           formData.append('psw',this.PSW);
           formData.append('new_psw',this.NewPSW);
           formData.append('id',this.$store.state.currentUser.id);
           formData.append('photo_change',this.photo_change);
           this.$api.user.updateUser(formData).then(res=>{
              if(res.data.status==="1"){
                  if(this.photo_change){
                     this.photo_change=false;
                     this.$store.state.currentUser.user_photo=this.$refs.user_photo.img;
                  }
                this.$store.state.currentUser.name=this.name;
                this.$store.state.currentUser.sign=this.sign;
                this.$store.state.currentUser.sex=this.sex;
                localStorage.user=JSON.stringify(this.$store.state.currentUser);
              }else{
                this.photo_change=false;this.$refs.user_photo.reSet();
                this.name=this.$store.state.currentUser.name;
                this.sign=this.$store.state.currentUser.sign=this.sign;
                this.sex=this.$store.state.currentUser.sex;
              }
           });
         },
         cancel(){
           let user=this.$store.state.currentUser;
           if(user.name===undefined)
             this.name='';
           else
             this.name=user.name;
           this.sex=user.sex;
           if(user.sign===undefined){
             this.sign='';
           }
           else{
             this.sign=user.sign;
           }
           this.photo_change=false;
           this.$refs.user_photo.reSet();
         }
    }
  }
</script>

<style scoped>
.info{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
  .item{
   display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-top: 20px;
  }
  .label{
    color:#9999A6 ;
    font-style: normal;
    width:80px;
    font-size: 14px;
    font-weight: bold;
  }
  input{
    background-color: white;
    border: 1px #9999A6 solid;
    height: 30px;
    border-radius: 6px;
    width: 200px;
    color: #9999A6;
    box-sizing: border-box;
    text-align: left;
    vertical-align: top;
    padding: 2px 10px;
    font-size: 13px;
  }
  input:focus{
    border-color: #FF0000;
  }
.sign{
  background-color: white;
  border: 1px #9999A6 solid;
  height: 80px;
  border-radius: 6px;
  width: 200px;
  color: #9999A6;
  box-sizing: border-box;
  text-align: left;
  vertical-align: top;
  padding: 2px 10px;
  font-size: 13px;
  outline: none;
}
.sign:focus{
  border-color: #FF0000;
}
  .set{
    position: absolute;
    top:1%;
    left: 70%;
    z-index: 5;
  }
 .action{
   display: flex;
   margin:40px 0;
   justify-content: space-between;
   align-items: center;
   flex-direction: row;
   width: 140px;
 }
</style>



