<template>
<div class="collections">
  <single-collection class="single_collection" v-for="(item,index) of collections" :key="index" :item="item"></single-collection>
</div>
</template>

<script>
  import SingleCollection from './Collection'
  export default {
    name: 'Collections',
    components: {SingleCollection},
    data(){
      return{
        collections:[]
      }
    },
    created () {
       this.$api.user.getCollections(this.$store.state.currentUser.id).then(res=>{
            this.collections=res.data;
       })
    }
  }
</script>

<style scoped>
.collections{
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 0 10%;
  font-size: 10%;
}
  .single_collection{
    width: 30%;
    margin: 10px;
  }
</style>



