<template>
    <div class="box">
      <div class="title">Login</div>
      <h3 id="cancel" @click="toggle">X</h3>
      <div class="content" style="margin-top: 50px">
          <h5>EMAIL</h5><input class="text" type="text"  v-model="ID">
      </div>
      <div class="content" style="margin-top: 30px">
        <h5>PASSWORD</h5>
        <input class="text" type="password" v-model="PSW">
      </div>
      <div class="login">LOGIN</div>
    </div>
</template>

<script>

  export default {
    name: 'Login',
    data(){
      return{

      }
    },methods:{
      toggle(){
        this.$emit('Login');
      }
    }
  }
</script>

<style scoped>
  .box{
   background-color: #1E8CD7;
   border: #EBD3BD solid 1px;
   text-align: center;
    border-radius: 2px;
    width: 420px;
    box-sizing: border-box;
    padding-bottom: 20px;
    position: fixed;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .title{
    color: #E3302B;
    font-size: 70px;
    margin-top: 40px;
  }
  .content{
     width: 100%;
      text-align: left;
    color: #EBD3BD;
  }
  .content h5{
    position: relative;
    left: 5%;
  }
  .content .text{
    border: #EBD3BD solid;
    width: 90%;
    height: 50px;
    position: relative;
    left: 5%;
    background-color: transparent;
    font-size: 20px;
    padding: 0 5px;
    box-sizing: border-box;
    color: #EBD3BD;
  }
  .content .text:focus{
    border-color: #E3302B;
    color:#E3302B ;
  }
  .login{
    background: #E3302B;
    width: 90%;
    position: relative;
    left: 5%;
    text-emphasis: "almda";
    height: 50px;
    margin-top: 30px;
    color: white;
    font-size: large;
     text-align: center;
    line-height: 50px;
    cursor: pointer;
  }
  #cancel{
    position: absolute;
    top:5px;
    left: 95%;
     color: #E3302B;
    cursor: pointer;
  }
  #cancel:hover{
    color: white;
  }

</style>



