<template>
    <div style="display: flex;justify-content: center;align-items: center;padding: 30px 0;background-color: transparent">
       <div class="collection" @mouseenter="isHover=true" @mouseleave="isHover=false" :style="{'filter':isHover?'drop-shadow(4px 2px 4px rgba(166,166,166,0.6))':''}">
             <photo :img="img[0]" :style="{'opacity':isHover?'1':''}" :pb="60" class="photo"></photo>

           <div class="name">
             <div style="width: fit-content;display: flex;align-items: center"><img src="/static/image/book.png" ><span style="font-size: 12px">{{item.title}}</span></div>
             <h5 style="color: #FF3535;font-size: 12px">2018-09-05</h5>
           </div>
           <div class="user">
             <div style="width: fit-content;display: flex;align-items: center"><img :src="item.user_photo"><span style="font-size: 14px">{{item.name}}</span></div>
             <div class="icon">
               <img src="/static/image/gray_collect.png"><span style="font-size: 12px;margin-right: 3px">{{item.collections}}</span><img src="/static/image/gray_heart.png"><span style="font-size: 12px">{{item.stars}}</span>
             </div>
           </div>
       </div>
    </div>
</template>

<script>
  import Photo from '../public/photo'
  export default {
    name: 'SingleCollection',
    components: {Photo},
    props:['item'],
    data(){
      return{
        isHover:false
      }
    },
    computed:{
      img(){
         return JSON.parse(this.item.imgs);
      }
    }
  }
</script>

<style scoped>
.collection{
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  background-color: white;
  padding: 20px 0;
  color: #7E7D8B;
  border: 1px solid rgba(217,217,217,0.4);
  transition: transform 0.2s;
  cursor: pointer;
}
.collection:hover{
   transform: translate(0,-8px);
}

.photo{
  width: 90%;
  position: relative;
  margin-bottom: 10px;
  opacity: 0.6;
}
.photo:hover{
  opacity: 1;
}
  .name{
    height: 20px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 3%;
    box-sizing: border-box;
  }
  .name>div>img{
    width: 15px;
    height:15px;
    margin-right: 3px;
  }
  .user{
    height: 25px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 3%;
    box-sizing: border-box;
  }
.user img{
  width: 22px;
  height:22px;
  margin-right: 3px;
  border-radius: 50%;
}
  .icon{
    width: fit-content;display: flex;align-items: center
  }
  .icon img{
    width: 15px;
    height: 15px;
  }
</style>



