<template>
    <div class="pin" :style="{width:pinw+'px'}">
      <a :href="box.url ">
        <img :src="box.aphoto">
        <div class="content" v-if="showuser">
          <div class="title">{{box.title}}</div>
          <img src="/static/image/love.png">
          <div class="stars">{{box.stars}}</div>
          <div class="des" > {{box.des}}</div>
        </div>
        <div class="hr" v-if="showuser"></div>
        <div class="userinfo" v-if="showuser">
          <img :src="box.user_photo" >
          <span class="name">{{box.name}}</span>
        </div>
      </a>
    </div>

</template>

<script>
export default {
  name: 'waterfallBox',
  props: ['box','index','showuser','pinw'],
  data () {
    return {
    }
  },
  mounted () {
    this.$nextTick(() => { //使用nextTick为了保证dom元素都已经渲染完毕

      this.$emit('set',this.$el.offsetHeight,this.index);
    });
  }
}
</script>

<style scoped>
  .pin{
    text-align: left;
    background: #FFF;
    border: 1px solid #ccc;
    box-shadow: 0px 0px 6px #ccc; /*中间投影*/
    border-radius: 5px; /*圆角*/
    position: absolute;
  }
  .pin a{
    width: 100%;

  }
  .pin>a>img{
    width:100%;
    vertical-align: middle;
  }
  .hr{
    width: 90%;
    background-color: antiquewhite;
    height: 2px;
    margin: 4px auto;
  }
  .pin .content{
    position: relative;
    width: 90%;
    margin: 4px auto;
    padding: 5px 4px;
    text-align: left;

  }
  .pin .content .title{
    position: relative;
    font-size: 15px;
   text-align: left;
    width: fit-content;
    display: inline-block;
    border-style: none;
  }
  .pin .content img{
    position: relative;
    height: 20px;
    top:4px;
    left: 3px;
    display: inline-block;
    border-style: none;
    width: 20px;
  }
  .pin .content .stars{
    position: relative;
    font-size: 9px;
    text-align: left;
    margin: 5px 0 0 4px;
    display: inline-block;
    width: fit-content;
    border-style: none;
  }
  .pin .content .des{
    width:100%;
    font-size: 12px;

  }
  /*设置每一个瀑布流块中的图像样式*/
 .pin  .userinfo{
   position: relative;
    width: 100%;
    height: 40px;
    text-align: left;
  }
 .pin  .userinfo>img{
    position: relative;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    overflow: hidden;
    top:50%;
    transform: translate(8px,-50%);
    display: inline-block;
  }
  .pin .userinfo .name{
    font-size: 14px;
    position:absolute;
    z-index: 5;
    top:50%;
    display: inline-block;
    transform: translate(15px,-50%);
  }
</style>



