<template>
    <!--<div  class="sHoverItem"  @mouseenter="toggle" @mouseleave="toggle" >
      <img  src="/static/image/4.jpg">
      <transition name="show">
      <div  class="sIntro" v-show="isShow">
        <div class="title">Flowers</div>
        <div class="des"> Flowers are so inconsistent! Flowers are so inconsistent!Flowers are so inconsistent!Flowers are so inconsistent!</div>
        <button>inconsistent</button>
      </div>
      </transition>
    </div>-->
  <div>
    <div class="sHoverItem" @mouseenter="isShow=true" @mouseleave="isShow=false">
      <photo :pb="75" style="width: 100%" :img="img"></photo>
      <transition name="show">
        <div  class="sIntro" v-show="isShow">
          <div class="title">{{card.title}}</div>
          <div class="des"> {{card.des}}</div>
          <button>inconsistent</button>
        </div>
      </transition>
    </div>
  </div>

</template>

<script>
  import Photo from '../public/photo'
  export default {
    name: 'CardItem',
    props:['card'],
    components: {Photo},
    data(){
      return{
        isShow:false
      }
    },
    methods:{
    },
    computed:{
         id(){
           return this.card.id;
         },
         aid(){
           return this.card.aid;
         },
         img(){
            let imgs=JSON.parse(this.card.imgs);
            return imgs[0];
         }
    }
  }
</script>

<style scoped>
  *{ margin:0; padding:0;box-sizing: inherit}
  .show-enter-active,.show-leave-active{
    transition:all  0.5s;
  }
  .show-enter,.show-leave-to{
     transform: scale(0);
  }
  .show-enter-to,.show-leave{
    transform: scale(1);
  }
  .sHoverItem{
    width: 100%;
    font-family: "arial";
    border: 6px solid #ccc;
    display: inline-block ;
    position: relative;
    box-sizing: border-box;
    overflow: hidden;

  }
  .sIntro{
    background-color:
      rgba(236,138,113,0.6);
    text-align: center;
    font-family: "arial";
    position: absolute ;
    top:-33%;
    left: -12.5%;
    width: 125%;
    height:166%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    overflow: hidden;
  }
  .sIntro .title{font-family: "arial"; font-size:25px; text-align: center; color: #FFF; border-bottom: 1px dashed #fff; padding-bottom: 20px;font-weight: bold}
  .sIntro .des{font-size:15px; text-align: center; color: #FFF;  color: white; padding: 15px;width: 60%}
  .sIntro button{font-family: "arial"; font-size:16px; color: white;  background-color: #0AB5DA; border:none; border-radius: 10px; }

</style>



