<template>
    <div style="background-color: white;text-align: left;padding:1% 2%;box-sizing: border-box;width: 100%;position: relative">
      <div class="upload">
        <div style="width: 120px">{{tip}}</div>
        <sb style="width: 60px" :bg1="'#EC414D'" :bg2="'#D80211'" :color1="'#ffffff'" :color2="'#ffffff'" :content="isFinish? '编辑':'完成'" @click.native="isFinish=!isFinish" v-show="edit"></sb>
        <sb style="width: 60px" :bg1="'#EC414D'" :bg2="'#D80211'" :color1="'#ffffff'" :color2="'#ffffff'" :content="'分享'" @click.native="upload" v-show="edit"></sb>
      </div>
      <div style="display: flex; flex-direction: column">
         <div class="title"  v-show="!edit">{{title}}</div>
         <input class="title" v-model="title" v-show="edit" placeholder="请输入您的标题">
         <input class="des" v-model="des" v-show="edit" placeholder="请输入您的描述">
         <div class="des" v-show="!edit">{{des}}</div>
      </div>
      <div class="album">
          <div style="width: 55%;display: inline-block">
               <photo  v-show="!edit" :img="photos[current]" :pb="60" style="width: 100%"  ></photo>
               <photo v-show="edit" :img="photos.length>0?getObjectURL(photos[current]):'./static/image/nothing.png'" :pb="60" style="width: 100%"  ></photo>
           </div>
          <div style="width: 40%;padding-left: 20px;padding-top: 10px;display: flex;flex-direction: column">
            <div class="photos">
              <div class="photo_item" v-for="(item,index) in photos" :key="item.id" :style="{'borderColor':current===index?'blue':'','opacity':current===index?'1':''}">
                <photo  class="photo" @click.native="select(index)"  :style="{'borderColor':current===index?'blue':'','opacity':current===index?'1':''}" :img="item" :pb="60" v-show="!edit"></photo>
                <photo  class="photo" @click.native="select(index)" :style="{'borderColor':current===index?'blue':'','opacity':current===index?'1':''}" :img="getObjectURL(item)" :pb="60" v-show="edit"></photo>
                <img :src="isDelete === index?'/static/image/red_delete.png':'/static/image/delete.png'" @mouseenter="isDelete=index" @mouseleave="isDelete=-1" v-show="edit" @click="delImg(index)">
              </div>
              <div class="photo_item" style="display: flex;justify-content: center;align-items: center;background-color: #e2e2e2" v-show="edit">
                <photo class="photo" :pb="60" :img="'./static/image/add.png'"  style="opacity: 0;"></photo>
                <photo class="add" :pb="100" :img="'./static/image/add.png'" ></photo>
                <input  class="addin" type="file"  @change="addImg" ref="inputer" multiple accept="image/png,image/jpeg,image/gif,image/jpg"/>
              </div>
            </div>
            <div class="small_title" v-show="!edit">{{titles[current]}}</div>
            <input class="small_title" v-show="edit" placeholder="请输入小标题" v-model="titles[current]">
            <div class="small_des" v-show="!edit" > {{deses[current]}}</div>
            <input class="small_des" v-show="edit" placeholder="请输入小描述" v-model="deses[current]">
          </div>

      </div>
    </div>
</template>

<script>
  import Photo from '../public/photo'
  import sb from '../public/sb'
  export default {
    name: 'Album',
    components: {Photo,sb},
    data(){
      return{
         edit:true,
         current:0,
         photos:[],
         title:'',
         des:'',
        titles:[],
         deses:[],
         isDelete:-1,
         isAdd:false,
        imgLen:0,
        fil:[],
        formData:new FormData(),
        tip:'',
        isFinish:false
      }
    },
   created(){
      if(this.$store.state.view==='2'){
        this.$api.article.getArticle({id:this.$store.state.to_id,aid:this.$store.state.to_aid}).then(res=>{
          console.log(this.$store.state.to_id+'  '+this.$store.state.to_aid); this.edit=false;
          let article=res.data;
          this.title=article.title;
          this.des=article.des;
          this.titles=JSON.parse(article.titles);
          this.deses=JSON.parse(article.deses);
          this.photos=JSON.parse(article.imgs);
        })
      }else  if(this.$store.state.view==='3'){
         this.edit=true;
         this.$emit('setP',3);
      }

    },
    methods:{
      select(position){
        this.current=position;

      },
      addImg(event){
        let inputDOM = this.$refs.inputer;
        // 通过DOM取文件数据
        this.fil = inputDOM.files;
        let oldLen=this.imgLen;
        let len=this.fil.length+oldLen;

        if(len>8){
          alert('最多可上传8张，您还可以上传'+(8-oldLen)+'张');
          return false;
        }
        for (let i=0; i < this.fil.length; i++) {
          let size = Math.floor(this.fil[i].size / 1024);
          if (size > 5*1024*1024) {
            alert('请选择5M以内的图片！');
            return false;
          }
          this.imgLen++;
          /*this.$set(this.photos,this.fil[i].name+'?'+new Date().getTime()+i,this.fil[i]);*/
          this.photos.push(this.fil[i]);
        }
      },
      getObjectURL(file) {
        console.log("获取图片地hi");
        if(!this.edit)
          return  null;
        let url = null ;
        if (window.createObjectURL!==undefined) { // basic
          url = window.createObjectURL(file) ;
        } else if (window.URL!==undefined) { // mozilla(firefox)
          url = window.URL.createObjectURL(file) ;
        } else if (window.webkitURL!==undefined) { // webkit or chrome
          url = window.webkitURL.createObjectURL(file) ;
        }
        return url ;
      },
      delImg(key){
        if(key===this.imgLen-1){
          if(this.imgLen>1 && key===this.current)
            this.current--;
        }
        else{
            if(this.current===this.imgLen-1 && this.imgLen>1)
              this.current--;
        }
        this.photos.splice(key,1);
        this.imgLen--;
        this.titles.splice(key,1);
        this.deses.splice(key,1);
      },
      upload(){
        if(!this.$store.state.isLogin){
          this.tip="还没有登录";
          return false;
        }
        if(typeof this.title == "undefined" || this.title  == null || this.title  === ""){
          this.tip="大标题不能为空";
          return false;
        }
        if(typeof this.des == "undefined" || this.des  == null || this.des  === ""){
          this.tip="描述不能为空";
          return false;
        }
        if(!this.isFinish){
          this.tip="请点击完成";
          return false;
        }
        if(this.imgLen===0){
          this.tip="什么都还没有🐷";
          return false;
        }
        if(this.imgLen<4){
          this.tip="最少上传四张图片🐷";
          return false;
        }
        this.tip="上传中...";
        this.formData=null;
        this.formData=new FormData();
        for(let i=0;i<this.imgLen;i++){
          this.formData.append('imgs',this.photos[i]);
        }
        this.formData.append('title',this.title);
        this.formData.append('des',this.des);
        for(let i=0;i<this.imgLen;i++){
          if(this.titles[i]===undefined)
            this.titles[i]='';
          this.formData.append('titles',this.titles[i]);
        }
        for(let i=0;i<this.imgLen;i++){
          if(this.deses[i]===undefined)
            this.deses[i]='';
          this.formData.append('deses',this.deses[i]);
        }
        this.formData.append('id',this.$store.state.currentUser.id);
        this.formData.append('sort','2');
        this.$api.article.upload(this.formData).then(res=>{
          if(res.data.status==='1'){
            this.tip="上传成功😘";
          }else{
            this.tip="上传失败...😭";
          }
        });
      }
    }
  }
</script>

<style scoped>
  input{
    border: 0;
    background-color: transparent;
    color: black;
  }
.title{
  font-size: 35px;
  font-weight: bold;
  color: black;
}
  .des{
     margin: 10px 0;
    color: black;
  }
  .add{
    width: 30%;
    opacity: 0.5;
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);

  }
  .add:hover{
     opacity: 1;
  }
  .addin{
    background-color: transparent;
    border: 0px;
    position: absolute;
    width: 100%;
    padding-bottom: 60%;
    opacity: 0;
    z-index: 5;
    cursor: pointer;
  }
  .album{
     width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;

  }
  .photo{
    width: 100%;
    opacity: 0.5;
  }
  .photos{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .photo_item{
    width: 25%;
    cursor: pointer;
    border: 3px white solid;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  .photo_item>img{
    width: 15%;
    position: absolute;
    top:1%;
    left:85%;
  }
.small_title{
    margin: 30px 10px 20px;
    font-size: 25px;
    font-weight: bold;
    color: black;
    border-bottom: 2px black solid;
    width: fit-content;
  }
.small_des{
  margin: 30px 10px 20px;
  color: black;
}
  .upload{
     position: absolute;
    top:2%;
    left: calc(100% - 270px);
     width: 260px;
    display: flex;
    flex-direction: row;
    color: #FF0000;
    justify-content: space-between;
    align-items: center;
  }
</style>



