<template>
  <div  style="background-color:#1ABC9C ;width: 400px;padding: 30px 10px;box-sizing: border-box">
    <div class="box" >
      <h2  @click="change(0)" ref="title" >{{titles[0]}}</h2>
      <h2  @click="change(1)" ref="title">{{titles[1]}}</h2>
      <h2  @click="change(2)" ref="title">{{titles[2]}}</h2>
    </div>
    <ul style="margin-top: 40px">
      <div id="triangle" :style="{left:tri_position[index]}"></div>
      <li ><transition name="show" ><input type="text" placeholder="Email" v-show="show[0]" v-model="ID"></transition></li>
      <li><transition name="show"><input  type="password" placeholder="Password" v-show="show[1]" v-model="PSW"></transition></li>
      <li><transition name="show"><input  type="password" placeholder="Repeat Password" v-show="show[2]" v-model="NewPSW"></transition></li>
      <li><button @click="onClick">{{titles[index]}}</button></li>
    </ul>
    <h3 id="cancel" @click="toggle" >X</h3>
  </div>

</template>

<script>
  export default {
    name: 'LoginAndRegister',
    data(){
      return{
        index:0,
        tri_position:['10%','48%','80%'],
        show:[true,true,false],
        titles:['Sign In','Sign Up','Reset'],
        ID:'',
        PSW:'',
        NewPSW:''
      }
    },
    methods:{
       change(position){
         let  a=document.getElementsByTagName("h2");
         a[this.index].style.color="#A3E4D7";
         a[position].style.color="#ffffff";
         this.index=position;
         switch (position) {
           case 0:
             this.show[0]=true;this.show[1]=true;this.show[2]=false;break;
           case 1:
             this.show[0]=true;this.show[1]=true;this.show[2]=true;break;
           case 2:
             this.show[0]=false;this.show[1]=false;this.show[2]=true;break;
         }
       },
       onClick(){
         switch (this.index) {
           case 1:
             let user={ ID:this.ID,PSW:this.PSW}
             this.$api.user.register(user
             ).then(res=> {
               /*注册成功逻辑*/
             });
             break;
           case  0:
             this.$api.user.login({id:this.ID,psw:this.PSW}).then(res=> {

               if(res.data.status==='1'){
                 this.$store.commit('setUser',res.data.data);
                 this.$store.commit('setStatus',true);
                 localStorage.isLogin=true;
                 localStorage.setItem('user',JSON.stringify(res.data.data));
                 /*localStorage.user=JSON.stringify(res.data.data);*/
                 this.toggle();
               }
              });
             break;
         }
       },
      toggle(){
          this.$emit('toggle');
      }
    },
   mounted () {
      if(this.$store.state.isLogin){
        let user=this.$store.state.currentUser;
           this.ID=user.ID;
      }
      this.change(0);
   }
  }
</script>

<style scoped>
  *{ margin:0; padding:0;box-sizing: inherit}
  .show-enter-active,.show-leave-active{
    transition:all  1s  linear;
  }
  .show-enter{
   height: 0; opacity: 0;top:-50px;
  }
  .show-enter-to,.show-leave{
   opacity: 1; height: 50px;top:0;
  }
  .box{
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    background-color: #1ABC9C;
    width: 90%;
    position: relative;
    left: 5%;
    color: #A3E4D7;
  }
  h2{
    cursor: pointer;
  }
  li{
    list-style: none;
    margin: 15px 0;
  }
  ul{
    width: 90%;
    position: relative;
    left: 5%;
  }
  input{
    width: 100%;
    height: 50px;
    border-radius: 10px;
    text-align: center;
    border: 0;
    font-size: 20px;
    position: relative;
  }
  button{
    width: 100%;
    height: 50px;
    border-radius: 10px;
    text-align: center;
    border: 0;
    font-size: 20px;
    background-color: #16A085;
    color: white;
  }
  button:hover{
    background-color: #188E77;
  }
  #triangle{
    position: absolute;
    width:0;
    border-width:15px;
    border-style:solid;
    border-color:transparent  transparent  white transparent; /* transparent 设置边框颜色透明 */
    top: -29px;
    left: 80%;/*10 48 80*/
  }
  #cancel{
    color: white;position: absolute;top: 1%;left: calc(100% - 30px);width: 30px;height: 30px;opacity: 0.5;
  }
  #cancel:hover{
    opacity: 1;
  }
</style>



