<template>
  <div>
    <form action="#">
      <input type="text" placeholder="Search here..." v-model="content">
      <button type="submit" @click="search">
        SEARCH
      </button>
    </form>
  </div>

</template>

<script>
  export default {
    name: 'Search',
    data(){
      return{
          content:''
      }
    },
    methods:{
          search(){
            let id='';
            if(this.$store.state.isLogin)
              id=this.$store.state.currentUser.id;
            alert(id);
             this.$api.action.search(this.content,id).then(res=>{
                  this.$emit('searchResult',res.data)
             })
          }
    }

  }
</script>

<style scoped>
  input,button{
    outline: none;
  }
  form{
    font-size:0;
  }
  input{
    height: 40px;
    width: 450px;
    background-color: #eee;
    border: none;
    border-radius: 4px 0 0 4px;
    padding-left: 10px;
  }
  button{
    height: 40px;
    width: 110px;
    background-color: rgba(210,64,64,0.9);
    border: none;
    position: relative;
    border-radius: 0 4px 4px 0;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
  }
  input::-webkit-input-placeholder{
    font-style: italic;
  }
  input:-moz-placeholder{
    font-style: italic;
  }
  input:-ms-input-placeholder{
    font-style: italic;
  }
  button:before{
    content: "";
    display: inline-block;
    border: 6px solid transparent;
    border-right-color:#d24040;
    position: absolute;
    right:100%;
    top:14px;
  }
  button:hover{
    background-color: rgb(210,64,64);
  }
</style>



