import Vue from 'vue';
import Vuex from 'vuex'
Vue.use(Vuex);

const  state={
     currentUser:{},
     isLogin:false,
     token:'',
     to_id:'',
     to_aid:'',
     isMine:false,
     view:''
}
const  mutations={
       setUser(state,user){
         state.currentUser=user;
       },
       setStatus(state,isLogin){
         state.isLogin=isLogin;
       },
       reLogin(state){
            if(localStorage.isLogin){
                state.currentUser=JSON.parse(localStorage.user);
                state.isLogin=true;
            }
           state.to_id=localStorage.to_id;
           state.to_aid=localStorage.to_aid;
           state.view=localStorage.view;
       },
      reSetTo(state,item){
         console.log("reset"+item.id+' '+item.aid);
              state.to_id=item.id;
              state.to_aid=item.aid;
              localStorage.to_id=item.id;
              localStorage.to_aid=item.aid;
              if(state.isLogin){
                if(state.currentUser.id===item.id){
                  state.isMine=true;localStorage.isMine=true;
                }
                else{
                  state.isMine=false;localStorage.isMine=false;
                }
              }else{
                state.isMine=false;localStorage.isMine=false;
              }

      },
      setView(state,view){
         state.view=view;
         localStorage.view=view;
         console.log("当前view:"+view);
      },
      setToid(state,to_id){
         state.to_id=to_id;
      }
};
export default new Vuex.Store({
    state,
    mutations,
    getters:{
       getToken: state =>{
           return state.token;
       },
       getIsLogin: state =>{
          return state.isLogin;
       },
      getUser:state=>{
         return state.currentUser;
      },
      getTo_id:state=>{
        return state.to_id;
      },
      getTo_aid:state=>{
        return state.to_aid;
      },
      getIsMine:state=>{
        return state.isMine;
      }
    }

})



