import base from './base'; // 导入接口域名列表
import axios from './http'; // 导入http中创建的axios实例
import qs from 'qs'; // 根据需求是否导入qs模块

const user = {
  register(params){
    return axios.post(`${base.pre}/user`, qs.stringify(params));
  },
  login(params){
    return axios.get(`${base.pre}/user`,{
      params:{
        id:params.id,
        psw:params.psw
      },
    });
  },
  updateUser(formData){
    return axios.post(`${base.pre}/user/update`, formData,{
      headers: {'Content-Type': 'false'}
    });
  },
  getCollections(id){
    return axios.get(`${base.pre}/user/collections`,{
      params:{
        id:id,
      },
    });
  },
  getUser(id){
    return axios.get(`${base.pre}/user/other`,{
      params:{
        id:id
      },
    });
  }
}

export default user;



