<template>
  <div style="width: 100%">
    <user-header style="margin-top: 80px"></user-header>
    <div class="all">
      <div class="titles" >
        <div class="title" v-for="(item,index) in titles" :key="item.id" @click="change(index)" :style="{'borderColor':index===current?'#FF0000':''}">{{item}}</div>
      </div>
      <div class="icons" >
        <icon class="icon" v-for="item in icons" :img="item.img" :back="item.back" :key="item.id"></icon>
      </div>
      <div style="width: 100%;padding-top: 70px;display: flex;justify-content: center;align-items: center">
        <!--<router-view></router-view>-->
        <!--<fans ></fans>-->
        <!--<collections></collections>-->
        <!-- <Reccomends></Reccomends>-->
        <component :is="currentView" style="width: 100%" @setP="change"></component>
      </div>
      <!-- <album>

       </album>-->
    </div>
  </div>

</template>

<script>
  import UserHeader from '../User/UserHeader'
  import Icon from '../Icon/Icon'
  import album from '../Content/Album'
  import collections from '../Content/Collections'
  import reccomends from '../Content/Reccomends'
  import fans from '../Content/Fans'
  import UserInfo from '../Content/UserInfo'
  export default {
    name: 'UserAlbum',
    components: {UserInfo, fans, reccomends, collections, album, Icon,UserHeader},
    props:[],
    data(){
      return{
        current:0,
        /*titles:['个人信息','分享','收藏','当前浏览'],
        titles_en:['user-info','reccomends','collections','album'],*/
        icons:[{img:'./static/image/focus.png',back:'#FF0000'},{img:'./static/image/collect.png',back:'#FFA000'},{img:'./static/image/heart.png',back:'#EA4C89'}],
        isMine:this.$store.state.isMine
      }
    },
    created(){
      this.$store.commit('reLogin');
    },
    methods:{
      change(position){
        this.current=position;
      }
    },
    computed:{
      titles(){
            if(this.$store.state.view==='1'){
              return ['个人信息','分享','收藏'];
            }else  if(this.$store.state.view==='2')
              return ['当前浏览','分享'];
            else if(this.$store.state.view==='3'){
                return ['个人信息','分享','收藏','正在编辑']
            }
      },
      titles_en(){
        if(this.$store.state.view==='1'){
          return ['user-info','reccomends','collections'];
        }
        else  if(this.$store.state.view==='2')
          return ['album','reccomends']
        else  if(this.$store.state.view==='3'){
          this.current=3;
          return ['user-info','reccomends','collections','album']
        }

      },
      currentView(){
        return this.titles_en[this.current];
      }
    }
  }
</script>

<style scoped>
  .all{
    background-color: #F5F5F5;
    position: relative;
    margin-bottom: 200px;
  }
  .titles{
    position: absolute;
    display: flex;
    top: 0;
    left: 13%;
    transform: translate(0,calc(-100.1% + 1px));
    color: white;
  }
  .title{
    width: fit-content;
    padding-bottom: 15px;
    height: 20px;
    border-bottom: transparent 3px solid;
    font-size: 15px;
    margin-right: 25px;
    cursor: pointer;
  }
  .icons{
    display: flex;
    position: absolute;
    justify-content: flex-start;
    transform: translate(-100%,-50%);
    left: 95%;
  }
  .icon{
    box-sizing: border-box;
  }
</style>



