<template>
  <div style="position: relative;cursor: pointer"  @mouseenter="isHover=true" @mouseleave="isHover=false">
    <photo class="avator" :img="img" :pb="100"></photo>
    <div class="camera">
      <img src="/static/image/camera.png" :style="{'opacity':isHover?'1':'0.8'}">
    </div>
    <input type="file"  @change="addImg" ref="inputer" multiple accept="image/png,image/jpeg,image/gif,image/jpg" />
  </div>

</template>

<script>
  import Photo from '../public/photo'

  export default {
    name: 'avator',
    components: {Photo},
    data(){
      return{
          isHover:false,
          fil:'',
          img:''
      }
    },
    mounted(){
      this.setImg();
    },
    methods:{
      setImg(){
         if(this.$store.state.isLogin)
           this.img=this.$store.state.currentUser.user_photo;
         else
            this.img='./static/image/undefined.png';
      },
      addImg(event){
        let inputDOM = this.$refs.inputer;
        // 通过DOM取文件数据
        this.fil = inputDOM.files[0];
          let size = Math.floor(this.fil.size / 1024);
          if (size > 5*1024*1024) {
            alert('请选择5M以内的图片！');
            return false;
          }
          this.img=this.getObjectURL(this.fil);
          this.$emit('change');
      },
      getObjectURL(file) {
        let url = null ;
        if (window.createObjectURL!==undefined) { // basic
          url = window.createObjectURL(file) ;
        } else if (window.URL!==undefined) { // mozilla(firefox)
          url = window.URL.createObjectURL(file) ;
        } else if (window.webkitURL!==undefined) { // webkit or chrome
          url = window.webkitURL.createObjectURL(file) ;
        }
        return url ;
      },
      reSet(){
         this.img=this.$store.state.currentUser.user_photo;
      }
    }
  }
</script>

<style scoped>
.avator{
   border-radius: 50%;
   overflow: hidden;
   box-sizing: border-box;
   position: relative;
  border: 0 solid rgba(255,255,255,0.5);
  width: 100%;
  height: 100%;
  cursor: pointer;
}
  .camera{
    position: absolute;
    border-radius: 50%;
    overflow: hidden;
    box-sizing: border-box;
    width: 20%;
    top:75%;
    left: 75%;
    cursor: pointer;

  }
  .camera>img{
    width: 100%;
    height: 100%;
  }
  input{
    opacity: 0;
    position: absolute;
    display: inline-block;
    height: 100%;
    width: 100%;
    top:0;
    left: 0;
  }
</style>



