<template>
  <div>
    <div class="photo" :style="s">
<!--      <img :src="img">-->
    </div>
  </div>

</template>

<script>
  export default {
    name: 'photo',
    props:['pb','img'],
    computed:{
      s:function () {
          let newstyle={};
          newstyle.paddingBottom=this.pb+'%';
          newstyle.backgroundImage="url("+this.img+")";
          return newstyle;
      }
    }
  }
</script>

<style scoped>
  .photo{
    width: 100%;
    position: relative;
    background-position: center;
    background-size: cover;
  }
  .photo>img{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
</style>



