<template>
  <div class="last">
    <div class="left"></div>
    <div class="left"></div>
  </div>
</template>

<script>
  export default {
    name: 'LastPage'
  }
</script>

<style scoped>
  .last{
    width: 120px;
    height: 50px;
    background-color: #999999;
    transition: background-color  1s,padding-right 0.5s;
    box-sizing: border-box;
    cursor: pointer;
    border-radius: 5px;
  }
  .last:hover{
    background-color: #FFE900;
    padding-right: 30px;
  }
  .left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 10px;
    margin-top: 15px;
  }
</style>



