<template>
  <div id="header" >
    <div class="left">
      <img src="/static/image/orange.png">
      <router-link to="/homepage" class="menu"><div  >热门</div></router-link>
      <div class="menu" >发现</div>
      <div class="menu" >生活</div>
      <div class="menu" >游戏</div>
      <div class="menu" >摄影</div>
      <div class="menu" >旅游</div>
      <div class="menu" >学习</div>
      <div class="menu" >美食</div>
      <div class="menu" >创作</div>
      <div class="menu" >二次元</div>
    </div>

    <div class="right">
      <photo class="avator" @click.native="isShow=!isShow" :pb="100" :img="img" @mouseenter.native="FloatWindowToggle" @mouseleave.native="FloatWindowToggle"></photo>
      <img src="/static/image/upload.png" @click="edit">
      <float-window class="floatwindow"  :showFloat="showFloat" @mouseenter.native="enterFloat=true" @mouseleave.native="enterFloat=false;showFloat=false"></float-window>
    </div>

    <LoginAndRegister class="show" v-show="isShow" @toggle="toggle"></LoginAndRegister>
  </div>
</template>
<script>
import Login from '../Login'
import LoginAndRegister from './LoginAndRegister'
import Photo from '../public/photo'
import FloatWindow from '../public/FloatWindow'
export default {
  name: 'guide',
  components: {FloatWindow, Photo, LoginAndRegister, Login},
  data(){
    return{
      isShow:false,
      showFloat:false,
      enterFloat:false
    }
  },
  computed:{
     img(){
         if(this.$store.state.isLogin){
            return this.$store.state.currentUser.user_photo;
         }else{
           return './static/image/undefined.png';
         }
     }
  },
  methods:{
    toggle(){
      this.isShow=!this.isShow;
    },
    FloatWindowToggle(){
      if(this.showFloat){
        setTimeout(() =>{
          if(!this.enterFloat)
          this.showFloat=!this.showFloat;
        },600);
      }else{
        this.showFloat=!this.showFloat;
      }


    },
    edit(){
        this.$router.push('/user');
        this.$store.commit('setView','3')
    }
  }
}
</script>

<style scoped>
  #header{
    background-color: transparent;
    height: 40px;
    position: fixed;
    width:99%;
    z-index: 999;
    top:0;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }
  .left{
     display: flex;
     flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
  }
     .left img{
    width: 25px;
    height: 25px;
    margin: 0 5px;
  }
  #header .menu{
    height:30px;
    color:white;
    font-size: 20px;
    cursor: pointer;
    opacity: 0.5;
    margin: 0 5px;
  }
  #header .menu:hover{
    opacity: 1;
  }
  .right{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    position: relative;
  }
  .right img{
    width: 30px;
    height: 30px;
    margin: auto 10px;
    opacity: 0.5;
    cursor: pointer;

  }
  .right img:hover{
    opacity: 1;
  }
  #header .avator {
    width: 25px;
    height: 25px;
    margin: 0 10px;
    cursor: pointer;
    border-radius: 50%;
    overflow: hidden;
  }

  .show{
    position: fixed;
    top:30%;
    left: calc(50% - 200px);
  }
  .show-enter-active,.show-leave-active{
    transition: opacity 0.5s;
  }
  .show-enter,.show-leave-to{
   opacity: 0;background-color: #42b983;
  }
  .show-enter-to,.show-leave{
  opacity: 1;background-color: #2c3e50;
  }
  .floatwindow{
    position: absolute;
    top:98%;
    left:0;
    z-index: 100;
  }
</style>



