<template>
     <div class="fans">
          <fan class="singlefan" v-for="item in fans" :key="item.id"></fan>
     </div>
</template>

<script>
  import Fan from './Fan'
  export default {
    name: 'Fans',
    components: {Fan},
    data(){
      return{
        fans:[{},{},{},{},{},{},{}]
      }
    }
  }
</script>

<style scoped>
.fans{
   display: flex;
   justify-content: space-between;
   background-color: #F5F5F5;
   flex-wrap: wrap;
   width: 96%;

}
  .singlefan{
    width: 15%;
    margin-bottom: 20px;
  }
</style>



