<template>
  <div contenteditable="true"
       v-html="innerText"
       @input="changeText"></div>
</template>

<script>
  export default {
    name: 'v_div',
    props: ['value'],
    data(){
      return {innerText:this.value}
    },
    methods:{
      changeText(){
        this.innerText = this.$el.innerHTML;
        this.$emit('input',this.innerText);
      }
    }
  }
</script>

<style scoped>

</style>



