<template>
  <div style="width: 80px;height: 80px;position: relative" @click="toggle">
    <div class="wave ripple danger" >
      <div class="circle" :style="{'animation':love?'':'none','borderColor':back}"></div>
      <div class="circle" :style="{'animation':love?'':'none','borderColor':back}"></div>
      <div class="circle" :style="{'animation':love?'':'none','borderColor':back}"></div>
      <div class="single-icon" :style="{'backgroundColor':back}">
        <img :src="img"  :style="{'transform':love?'scale(1.5)':''}">
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: 'Icon',
    props:['img','back'],
    data(){
      return{
        love:false,
      }
    },
    methods:{
      toggle(){
        this.love=!this.love;

      }
    }
  }
</script>

<style scoped>
  .single-icon{
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    width: 60%;
    height: 60%;
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-45%,-45%);
  }
  img{
    width: 40%;
    height: 40%;
    transition: transform 0.5s;
    z-index: 2;
  }

  .wave {
    position: relative;
    width: 80px;
    height: 80px;
    text-align: center;
    line-height: 100px;
    font-size: 28px;
  }

  .wave .circle {
    position: absolute;
    border-radius: 50%;
    opacity: 0;
  }

  /* 波纹效果 */
  .wave.ripple .circle {
    width: calc(100% - 6px); /* 减去边框的大小 */
    height: calc(100% - 6px);/* 减去边框的大小 */
    border: 6px solid #fff;
  }

  .wave.ripple .circle:first-child {
    animation: circle-opacity 2s infinite;
  }

  .wave.ripple .circle:nth-child(2) {
    animation: circle-opacity 2s infinite;
    animation-delay: .3s;
  }

  .wave.ripple .circle:nth-child(3) {
    animation: circle-opacity 2s infinite;
    animation-delay: .6s;
  }

  .wave.ripple.danger {
    color: red;
  }

  .wave.ripple.danger .circle {
    border-color: red;
  }

  .wave.ripple.warning {
    color: orange;
  }

  .wave.ripple.warning .circle {
    border-color: orange;
  }

  /* 波动效果 */
  .wave.solid .circle{
    width: 100%;
    height: 100%;
    background: #fff;
  }

  .wave.solid .circle:first-child {
    animation: circle-opacity 2s infinite;
  }

  .wave.solid.danger {
    color: red;
  }

  .wave.solid.danger .circle{
    background: red;
  }

  .wave.solid.warning {
    color: orange;
  }

  .wave.solid.warning .circle{
    background: orange;
  }

  @keyframes circle-opacity{
    from {
      opacity: 1;
      transform: scale(0);
    }
    to {
      opacity: 0;
      transform: scale(1);
    }
  }

</style>



