<template>
    <div class="fw" v-show="showFloat">
         <div class="item"><router-link class="text" to="/user" @click.native="setView">设置</router-link></div>
         <div class="item"><div>退出</div></div>
    </div>
</template>
<script>
  export default {
    name: 'FloatWindow',
    props:['showFloat'],
    data(){
       return{
       }
    },
    methods:{
       setView(){
         this.$store.commit('setView','1') ;this.$store.commit('setToid',this.$store.state.currentUser.id);
       }
    }
  }
</script>

<style scoped>
.fw{
   width: 55px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #090723;
}
  .item{
    box-sizing: border-box;
    margin: 4px 0;
    width: 55px;
    height: 30px;
    text-align: center;
    font-size: 13px;
    cursor: pointer;
    background-color: #090723;
    color: rgba(255,255,255,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .item:hover{
    background-color: rgba(255,255,255,0.2);
    color: rgba(255,255,255,1);
  }
  .text{
    background-color: transparent;
    color: rgba(255,255,255,0.5);
  }
  .text:hover{
    color: rgba(255,255,255,1);
  }
</style>



