<template>
    <div>
       <div class="button" :style="bg" @mouseenter="isEnter=true" @mouseleave="isEnter=false">
            <div class="text" :style="tc">
              {{content}}
            </div>
       </div>
    </div>
</template>

<script>
  export default {
    name: 'sb',
    props:['bg1','bg2','color1','color2','content'],
    data(){
      return{
           isEnter:false
      }
    },
    computed:{
       bg(){
         if(this.isEnter)
           return{
           backgroundColor:this.bg2
           }
         else
           return{
             backgroundColor:this.bg1
           }
       },
       tc(){
         if(this.isEnter)
           return{
           color:this.color2
           }
         else
           return{
             color:this.color1
           }
       }
    }
  }
</script>

<style scoped>
.button{
   width: 100%;
   padding-bottom: 56%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 3px;
  cursor: pointer;
}
  .text{
      font-size: 13px;
       position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);

  }
</style>



