<template>
     <div class="userheader">
       <div class="left">
         <photo style="width: 80px;margin-bottom: 10px;border-radius: 50%;overflow: hidden" :pb="100" :img="user.user_photo"></photo>
         <div class="lovers">
             <div class="text" style="margin-right: 10px">
               <h5>{{user.fans}}</h5>
               <h6>粉丝</h6>
             </div>
             <div class="text">
               <h5>{{user.focus}}</h5>
               <h6>关注</h6>
             </div>
         </div>
     </div>
     <div class="right">
             <h2>{{user.name}}</h2>
     </div>
     </div>
</template>

<script>
  import Avator from './avator'
  import Photo from '../public/photo'
  export default {
    name: 'UserHeader',
    components: {Photo, Avator},
    data(){
      return{
           user:{}
      }
    },
    created () {
      console.log("header"+this.$store.state.to_id+' '+this.$store.state.currentUser.id);
      this.set();

    },
    methods:{
        set(){
          if(this.$store.state.to_id===this.$store.state.currentUser.id){
            this.user=this.$store.state.currentUser;
          }
          else {
            this.$api.user.getUser(this.$store.state.to_id).then(res=>{
              this.user=res.data;
            })
          }

        }
    }
  }
</script>

<style scoped>
.userheader{
   height: 140px;
  display: flex;
  font-style: normal;
}
  .left{
     display: flex;
     flex-direction: column;
    width: 150px;
    height: 100%;
    align-items: center;
  }
  .left .lovers{
    display: flex;
    flex-direction: row;
    justify-content:space-between;

  }
  .text{
    display: flex;
    flex-direction: column;
    align-items: center;
     height:20px;
     line-height:20px;
    color: #FAFAFB;
    opacity: 0.8;
  }
  .text:hover{
    color: white;
  }
  .right{
    color: white;
    text-align: left;
    margin-left: 50px;
     height: 100%;
    margin-top: 30px;

  }

</style>



